.about__me {
    margin: 0 auto;
    padding: 2em;
    border-radius: 1rem;
    background: var(--secondary-button);
    color: var(--color-dark);
    position: relative;
    outline: none;
}

.about__me::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%) translateY(-100%);
    border-left: 1rem solid transparent;
    border-right: 1rem solid transparent;
    border-bottom: 1rem solid var(--secondary-button);
  }

